import PokemonCard from "./PokemonCard";
import "./css/PokemonsBox.css";

const PokemonsBox = ({ pokemons, images, filter, type, sort }) => {
	if (sort === "asc-id") {
		pokemons.sort((a, b) => {
			return Number(a.id) - Number(b.id);
		});
	} else if (sort === "des-id") {
		pokemons.sort((a, b) => {
			return Number(b.id) - Number(a.id);
		});
	} else if (sort === "asc-name") {
		pokemons.sort((a, b) => {
			let nameA = a.name.toLowerCase(),
				nameB = b.name.toLowerCase();

			if (nameA < nameB) {
				return -1;
			}
			if (nameA > nameB) {
				return 1;
			}
			return 0;
		});
	} else if (sort === "des-name") {
		pokemons.sort((a, b) => {
			let nameA = a.name.toLowerCase(),
				nameB = b.name.toLowerCase();

			if (nameB < nameA) {
				return -1;
			}
			if (nameB > nameA) {
				return 1;
			}
			return 0;
		});
	}
	return (
		<div className="div-pokemons-box">
			{filter === false
				? pokemons.map((pokemon) => (
						<PokemonCard pokemon={pokemon} images={images} />
				  ))
				: pokemons
						.filter((pokemon) => pokemon.type.includes(type))
						.map((pokemonFiltered) => (
							<PokemonCard pokemon={pokemonFiltered} images={images} />
						))}
		</div>
	);
};

export default PokemonsBox;
