import "./css/PokemonCard.css";
import { Link } from "react-router-dom";

const PokemonCard = ({ pokemon, images }) => {
	return (
		<div className="div-pokemon-card">
			<Link
				to={"/pokemons/" + pokemon.name.trim().replace(/\s/g, "").toLowerCase()}
			>
				<div className="div-pokemon-image">
					<img src={images[pokemon.name]} alt={pokemon.name} />
				</div>
				<div className="div-pokemon-info">
					<p>
						<small>#{pokemon.id.padStart(3, "0")}</small>
					</p>
					<h2>
						<span className="material-icons">catching_pokemon</span>
						{pokemon.name}
					</h2>
					<p>{pokemon.species}</p>
					<p>
						{pokemon.type.map((type) => (
							<span className="type">{type}</span>
						))}
					</p>
				</div>
			</Link>
		</div>
	);
};

export default PokemonCard;
