import "./css/PokemonModal.css";
import { Link } from "react-router-dom";

const PokemonModal = ({ pokemons, pokemon, images, prevLocation }) => {
	const displayEvolutionOne = () => {
		return (
			<Link
				to={
					"/pokemons/" +
					pokemon.evolution[0].trim().replace(/\s/g, "").toLowerCase()
				}
			>
				<div>
					<div className="div-image-container">
						<img
							src={images[pokemon.evolution[0]]}
							alt={pokemon.evolution[0]}
						/>
					</div>
					<p>
						<small>{pokemon.evolution[0]}</small>
					</p>
					<p className="p-pokemon-evolution-info">
						{pokemons
							.filter(
								(pokemonName) => pokemonName.name === pokemon.evolution[0]
							)
							.map((filteredPokemonName) => (
								<small className="small-pokemon-id">
									#{filteredPokemonName.id.padStart(3, "0")}
								</small>
							))}
						{pokemons
							.filter(
								(pokemonName) => pokemonName.name === pokemon.evolution[0]
							)
							.map((filteredPokemonName) =>
								filteredPokemonName.type.map((filteredPokemonType) => (
									<small className="small-pokemon-type">
										{filteredPokemonType}
									</small>
								))
							)}
					</p>
				</div>
			</Link>
		);
	};

	const displayEvolutionTwo = () => {
		return (
			<Link
				to={
					"/pokemons/" +
					pokemon.evolution[1].trim().replace(/\s/g, "").toLowerCase()
				}
			>
				<div>
					<div className="div-image-container">
						<img
							src={images[pokemon.evolution[1]]}
							alt={pokemon.evolution[1]}
						/>
					</div>
					<p>
						<small>{pokemon.evolution[1]}</small>
					</p>
					<p className="p-pokemon-evolution-info">
						{pokemons
							.filter(
								(pokemonName) => pokemonName.name === pokemon.evolution[1]
							)
							.map((filteredPokemonName) => (
								<small className="small-pokemon-id">
									#{filteredPokemonName.id.padStart(3, "0")}
								</small>
							))}
						{pokemons
							.filter(
								(pokemonName) => pokemonName.name === pokemon.evolution[1]
							)
							.map((filteredPokemonName) =>
								filteredPokemonName.type.map((filteredPokemonType) => (
									<small className="small-pokemon-type">
										{filteredPokemonType}
									</small>
								))
							)}
					</p>
				</div>
			</Link>
		);
	};

	const displayEvolutionThree = () => {
		return (
			<Link
				to={
					"/pokemons/" +
					pokemon.evolution[2].trim().replace(/\s/g, "").toLowerCase()
				}
			>
				<div>
					<div className="div-image-container">
						<img
							src={images[pokemon.evolution[2]]}
							alt={pokemon.evolution[2]}
						/>
					</div>
					<p>
						<small>{pokemon.evolution[2]}</small>
					</p>
					<p className="p-pokemon-evolution-info">
						{pokemons
							.filter(
								(pokemonName) => pokemonName.name === pokemon.evolution[2]
							)
							.map((filteredPokemonName) => (
								<small className="small-pokemon-id">
									#{filteredPokemonName.id.padStart(3, "0")}
								</small>
							))}

						{pokemons
							.filter(
								(pokemonName) => pokemonName.name === pokemon.evolution[2]
							)
							.map((filteredPokemonName) =>
								filteredPokemonName.type.map((filteredPokemonType) => (
									<small className="small-pokemon-type">
										{filteredPokemonType}
									</small>
								))
							)}
					</p>
				</div>
			</Link>
		);
	};
	return (
		<div className="div-pokemon-modal-container">
			<div className="div-pokemon-modal">
				<div className="div-pokemon-modal-left">
					<div className="div-pokemon-info">
						<p>
							<small>#{pokemon.id.padStart(3, "0")}</small>
							{pokemon.type.map((type) => (
								<span className="type">{type}</span>
							))}
						</p>
						<h2>
							<span className="material-icons">catching_pokemon</span>
							{pokemon.name}
						</h2>
						<p>{pokemon.species}</p>
					</div>
					<img src={images[pokemon.name]} alt={pokemon.name} />
					<div className="div-pokemon-evolution">
						{pokemon.evolution.length >= 1 ? displayEvolutionOne() : null}
						{pokemon.evolution.length >= 2 ? (
							<p>
								<span class="material-icons">double_arrow</span>
							</p>
						) : null}
						{pokemon.evolution.length >= 2 ? displayEvolutionTwo() : null}
						{pokemon.evolution.length >= 3 ? (
							<p>
								<span class="material-icons">double_arrow</span>
							</p>
						) : null}
						{pokemon.evolution.length >= 3 ? displayEvolutionThree() : null}
					</div>
				</div>
				<div className="div-pokemon-modal-right">
					<div className="div-pokemon-characteristics">
						<p>
							Height: <span>{pokemon.height}</span>
						</p>
						<p>
							Weight: <span>{pokemon.weight}</span>
						</p>
						<p>
							Abilities:
							{pokemon.abilities.map((ability) => {
								return <span>{ability}</span>;
							})}
						</p>
					</div>
					<div className="div-pokemon-stats">
						<p>
							Hp
							<br />
							<div className="bar-container">
								<p>{pokemon.stats.hp}</p>
								<div
									className="bar-fill"
									style={{ width: pokemon.stats.hp + "%" }}
								></div>
							</div>
						</p>

						<p>
							Attack
							<br />
							<div className="bar-container">
								<p>{pokemon.stats.attack}</p>
								<div
									className="bar-fill"
									style={{ width: pokemon.stats.attack + "%" }}
								></div>
							</div>
						</p>

						<p>
							Defense
							<br />
							<div className="bar-container">
								<p>{pokemon.stats.defense}</p>
								<div
									className="bar-fill"
									style={{ width: pokemon.stats.defense + "%" }}
								></div>
							</div>
						</p>

						<p>
							Special attack
							<br />
							<div className="bar-container">
								<p>{pokemon.stats.spatk}</p>
								<div
									className="bar-fill"
									style={{ width: pokemon.stats.spatk + "%" }}
								></div>
							</div>
						</p>

						<p>
							Special defense
							<br />
							<div className="bar-container">
								<p>{pokemon.stats.spdef}</p>
								<div
									className="bar-fill"
									style={{ width: pokemon.stats.spdef + "%" }}
								></div>
							</div>
						</p>

						<p>
							Speed
							<br />
							<div className="bar-container">
								<p>{pokemon.stats.speed}</p>
								<div
									className="bar-fill"
									style={{ width: pokemon.stats.speed + "%" }}
								></div>
							</div>
						</p>

						<p>
							Total
							<br />
							<div className="bar-container">
								<p>{pokemon.stats.total}</p>
								<div
									className="bar-fill"
									style={{ width: Math.round(pokemon.stats.total / 6) + "%" }}
								></div>
							</div>
						</p>
					</div>

					<Link className="btn-back" to={prevLocation}>
						Back
					</Link>
				</div>
			</div>
		</div>
	);
};

export default PokemonModal;
