import { Link } from "react-router-dom";

import "./css/PokemonNav.css";
const PokemonsNav = ({ types, sort, setSort, setPrevLocation }) => {
	const linkClickHandler = (e) => {
		const type = e.target.textContent;
		const links = document.querySelectorAll(
			"#div-pokemons-nav > #div-pokemons-nav-links > *"
		);
		links.forEach((link) => {
			if (link.textContent == type) {
				link.classList.add("link-active");
			} else {
				link.classList.remove("link-active");
			}
		});

		if (e.target.textContent === "All") {
			setPrevLocation("/");
		} else {
			setPrevLocation("/type/" + e.target.textContent.toLowerCase());
		}
	};

	const selectSortChangeHandler = (e) => {
		const selectSort = e.target.value;
		setSort(selectSort);
	};

	return (
		<div id="div-pokemons-nav">
			<div id="div-pokemons-nav-links">
				<Link to="/" className="link-active" onClick={linkClickHandler}>
					All
				</Link>
				{types.map((type) => (
					<Link to={"/type/" + type.toLowerCase()} onClick={linkClickHandler}>
						{type}
					</Link>
				))}
			</div>
			<div>
				<span class="material-icons">filter_alt</span>
				<select onChange={selectSortChangeHandler} value={sort}>
					<option value="asc-id">Ascend by id</option>
					<option value="des-id">Descend by id</option>
					<option value="asc-name">Ascend by name</option>
					<option value="des-name">Descend by name</option>
				</select>
			</div>
		</div>
	);
};

export default PokemonsNav;
